*{
	margin: 0;
	padding: 0;
	font-family: '微软雅黑';
	list-style: none;
	transition: all 0.5s;
}
.title{
	text-align: center;
	height: 15vh;
	line-height: 45px;
	background-color: #585858;
}
.title .todoTitle{
	/*display: inline-block;*/
	color: #fff;
}
.title .inp{
	width: 200px;
	height: 26px;
	margin-left: 15px;
	vertical-align: center;
	/*outline: none;*/
	border: none;
	border-radius: 4px 0px 0px 4px;
}
.title button{
	width: 50px;
	height: 26px;
	border: none;
	vertical-align: center;
	border-radius: 0px 4px 4px 0px;
}
.content{
	width: 80vw;
	margin: 0 auto;
}
.content .unfinish{
	margin-top: 20px;
	margin-bottom: 30px;
}
hr{
	margin: 20px 0;
}
.finish li,.unfinish li{
	width: 76vw;
	margin: 20px auto;
	height: 35px;
	line-height: 35px;
	
}
.finish li input,.unfinish li input{
	width: 15px;
	height: 15px;
	margin-left: 15px;
	vertical-align: center;
}
.finish li button,.unfinish li button{
	float: right;
	border: none;
	height: 26px;
	width: 50px;
	border-radius: 5px;
	margin-top: 5px;
	margin-right: 10px;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6)
}
li:hover{
	background-color:#F14900;
	color: #fff;
	box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.4)
}
.finish li span,.unfinish li span{
	vertical-align: center;
	height: 35px;
	line-height: 35px;
	margin-left: 15px;
}
